@charset "UTF-8";
:root {
  --inner-padding: 30px;
}

@media (max-width: 767px) {
  :root {
    --inner-padding: 20px;
    --max-width: 500px;
  }
}
:root {
  --font-family-base: "Noto Sans JP", "Noto Sans", sans-serif;
  --font-family-second: "Noto Serif JP", "Noto Serif", serif;
}

:root {
  --font-weight-regular: 400;
  --font-weight-normal: 500;
  --font-weight-semibold: 600;
  --font-weight-bold: 700;
  --font-weight-extra-bold: 800;
}

:root {
  --black: #000;
  --white: #fff;
  --bg-color: #FFF8ED;
  --main-yellow: #ADA142;
  --main-red: #832323;
}

:root {
  --z-index-header: 100;
  --z-index-to-top: 200;
  --z-index-cta-btn: 200;
  --z-index-drawer: 1000;
  --z-index-hamburger: 2000;
  --z-index-modal: 10000;
}

:root {
  --cq-ref-mobile: 390;
  --cq-ref: 1366;
  --fluid-basis-mobile: (100vw - 320px) / (390 - 320);
  --fluid-basis: (100vw - 768px) / (1366 - 768);
  --fluid-basis-2: (100vw - 1366px) / (1800 - 1366);
}

:root {
  --duration-short: 0.2s;
  --duration-base: 0.3s;
  --duration-long: 0.6s;
  --duration-fv: 0.7s;
  --ease-smooth-bounce: cubic-bezier(.48, .07, .33, .79);
}

body {
  color: var(--black);
  font-family: var(--font-family-base);
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.3913043478vw;
  }
}
@media (min-width: 1150px) {
  html {
    font-size: 16px;
  }
}

body {
  background-color: var(--bg-color);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  overflow-wrap: anywhere;
  word-break: normal;
  line-break: strict;
  text-rendering: optimizeLegibility;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

ul,
ol,
button,
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd,
time,
small {
  margin: 0;
  padding: 0;
}

ul,
ol {
  list-style: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

a,
span,
button {
  display: inline-block;
}

small,
time,
figure {
  border: 0;
  font: inherit;
  font-size: 100%;
  vertical-align: baseline;
}

address {
  font-style: normal;
}

img {
  max-width: 100%;
  display: block;
  width: 100%;
}

article > * + * {
  margin-top: 1em;
}

input,
button,
textarea,
select {
  font: inherit;
}

input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  clip-path: polygon(0 0, 0 0, 0 0, 0 0);
  border: 0;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  color: inherit;
}

@media (any-hover: hover) {
  a:hover {
    cursor: pointer;
  }
}
@media (min-width: 768px) {
  a[href*="tel:"] {
    pointer-events: none;
    cursor: default;
    text-decoration: none;
  }
}
a {
  -webkit-tap-highlight-color: transparent;
}

button {
  -webkit-tap-highlight-color: transparent;
}

.inner {
  width: 100%;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
  max-width: 500px;
}
@media screen and (min-width: 768px) {
  .inner {
    max-width: 1220px;
    padding-right: 35px;
    padding-left: 35px;
  }
}

body.page-template-page-contact-thanks,
body.post-type-archive-news,
body.post-type-archive-products {
  display: flex;
  flex-direction: column;
  height: 100vh;
  height: 100dvh;
}

body.page-template-page-contact-thanks .footer-wrapper,
body.post-type-archive-news .footer-wrapper,
body.post-type-archive-products .footer-wrapper {
  margin-top: auto;
}

.not-found {
  margin-top: 3.125rem;
  padding-bottom: 3.5375rem;
}
@media screen and (min-width: 768px) {
  .not-found {
    margin-top: 5.3125rem;
    padding-bottom: 6.85rem;
  }
}

.not-found__inner {
  margin-inline: auto;
  max-width: var(--max-width);
  padding-inline: var(--inner-padding);
}
@media screen and (min-width: 768px) {
  .not-found__inner {
    max-width: calc(902px + var(--inner-padding) * 2);
  }
}

.not-found__title {
  font-size: 1.25rem;
  font-family: var(--font-family-second);
  font-weight: var(--font-weight-normal);
  line-height: 1.3;
  letter-spacing: 0.1em;
  text-align: center;
}
@media screen and (min-width: 768px) {
  .not-found__title {
    font-size: 1.5625rem;
  }
}

.not-found__text {
  font-size: 0.9375rem;
  font-weight: var(--font-weight-regular);
  line-height: 2;
  letter-spacing: 0.1em;
  text-align: center;
  margin-top: 1.4375rem;
}
@media screen and (min-width: 768px) {
  .not-found__text {
    font-size: 1rem;
    margin-top: 2.0625rem;
  }
}

.not-found__top-link-wrap {
  text-align: center;
  margin-top: 2.0625rem;
}
@media screen and (min-width: 768px) {
  .not-found__top-link-wrap {
    margin-top: 2.6875rem;
  }
}

.contact-form {
  margin-top: 1.9375rem;
  padding-bottom: 4.0625rem;
  padding-top: 2.375rem;
  background-color: var(--white);
  position: relative;
}
@media screen and (min-width: 768px) {
  .contact-form {
    margin-top: 2.0625rem;
    padding-bottom: 2.1rem;
    padding-top: 1.25rem;
  }
}

@media (min-width: 768px) {
  .contact-form__list,
  .contact-form__intro,
  .contact-form__note {
    transform: translateX(-2.1875rem);
  }
}

.contact-form__bg {
  position: absolute;
  top: -10vw;
  left: 0;
  width: 100%;
  height: fit-content;
  object-fit: cover;
  aspect-ratio: 1399/321;
  z-index: -1;
}
@media screen and (min-width: 768px) {
  .contact-form__bg {
    top: -9.5vw;
  }
}

.contact-form__inner {
  margin-inline: auto;
  padding-inline: var(--inner-padding);
  max-width: 500px;
}
@media screen and (min-width: 768px) {
  .contact-form__inner {
    --inner-padding: 80px;
    max-width: calc(769px + 2 * var(--inner-padding));
  }
}

.contact-form__intro {
  font-size: 0.9375rem;
  font-weight: var(--font-weight-normal);
  line-height: 2.66667;
  letter-spacing: 0.1em;
}

.contact-form__note {
  font-size: 0.9375rem;
  font-weight: var(--font-weight-regular);
  line-height: 2;
  letter-spacing: 0.1em;
  margin-top: 1.4375rem;
  display: flex;
  align-items: center;
  gap: 0.4375rem;
}
@media screen and (min-width: 768px) {
  .contact-form__note {
    margin-top: 1.875rem;
  }
}

.contact-form__list {
  display: flex;
  flex-direction: column;
  gap: 0.9375rem;
  margin-top: 3.9375rem;
}
@media screen and (min-width: 768px) {
  .contact-form__list {
    margin-top: 2.1875rem;
    gap: 1.25rem;
  }
}

.contact-form__item {
  display: grid;
}
@media (max-width: 767px) {
  .contact-form__item {
    gap: 0.375rem;
  }
}
@media screen and (min-width: 768px) {
  .contact-form__item {
    grid-template-columns: 14.0625rem auto;
    align-items: center;
  }
}

@media screen and (min-width: 768px) {
  .contact-form__item--textarea {
    align-items: flex-start;
  }
}
@media screen and (min-width: 768px) {
  .contact-form__item--textarea dt {
    margin-top: 1.0625rem;
  }
}

.contact-form__label.contact-form__label--column {
  align-items: flex-start;
}

.contact-form__label {
  font-size: 0.9375rem;
  font-weight: var(--font-weight-normal);
  line-height: 2;
  letter-spacing: 0.1em;
  display: flex;
  align-items: center;
  gap: 0.4375rem;
}

.contact-form__label-text {
  line-height: 1;
}

.contact-form__none-required {
  content: "";
  width: 0.9375rem;
  aspect-ratio: 17/15;
  background-color: transparent;
  border-radius: calc(infinity * 1px);
  display: inline-block;
}
@media (max-width: 767px) {
  .contact-form__none-required {
    display: none;
  }
}

.contact-form__required {
  content: "";
  width: 0.9375rem;
  aspect-ratio: 17/15;
  background-color: var(--main-red);
  border-radius: calc(infinity * 1px);
  display: inline-block;
}

.contact-form__label-note {
  font-family: "Noto Sans", sans-serif;
  font-size: 0.8125rem;
  font-weight: var(--font-weight-normal);
  letter-spacing: 0.05em;
}

.contact-form__input input,
.contact-form__input textarea {
  background-color: var(--white);
  font-size: 0.9375rem;
  font-weight: var(--font-weight-normal);
  letter-spacing: 0.1em;
  width: 100%;
  padding: 1.03125rem 0.9375rem 1.03125rem 0.9375rem;
  border-radius: 10px;
  border: 2px solid var(--main-red);
  background: #FFF;
}

.contact-form__input textarea {
  height: 180px;
}
@media screen and (min-width: 768px) {
  .contact-form__input textarea {
    height: 227px;
  }
}

.contact-form__label--mt {
  display: flex;
  flex-direction: column;
}
@media screen and (min-width: 768px) {
  .contact-form__label--mt {
    margin-top: 0.9375rem;
  }
}

.contact-form__submit {
  text-align: center;
  margin-top: 3.0625rem;
}
@media screen and (min-width: 768px) {
  .contact-form__submit {
    margin-top: 4.125rem;
  }
}

.contact-form__submit-btn {
  font-family: var(--font-family-second);
  font-size: 0.9375rem;
  font-weight: var(--font-weight-normal);
  letter-spacing: 2em;
  text-indent: 2em;
  border-radius: calc(infinity * 1px);
  background-color: var(--main-yellow);
  color: var(--white);
  border: 2px solid var(--main-yellow);
  padding: 1.0625rem 0.9375rem;
  min-width: 17.5rem;
  text-align: center;
  position: relative;
  width: fit-content;
  transition: background-color var(--duration-base) ease-in-out, color var(--duration-base) ease-in-out;
}
@media (any-hover: hover) {
  .contact-form__submit-btn:hover {
    background-color: var(--white);
    color: var(--main-yellow);
  }
}
@media (any-hover: hover) {
  .contact-form__submit-btn:hover::after {
    background-color: var(--main-yellow);
    right: 1.375rem;
  }
}
.contact-form__submit-btn::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 1.6875rem;
  transform: translateY(-50%);
  width: 0.5375rem;
  background-color: var(--white);
  border-radius: calc(infinity * 1px);
  aspect-ratio: 1/1;
  transition: background-color var(--duration-base) ease-in-out, right var(--duration-base) ease-in-out;
  will-change: right, background-color;
}

.contact-form__btn {
  margin-top: 3.0625rem;
  text-align: center;
  padding-bottom: 3.5375rem;
}
@media screen and (min-width: 768px) {
  .contact-form__btn {
    margin-top: 2.25rem;
    padding-bottom: 6.85rem;
  }
}

input:user-invalid,
textarea:user-invalid {
  border: 1px solid red;
}

textarea:user-invalid + .error-message {
  display: block;
}

.error-message,
.format-error-message,
.email-mismatch-error {
  color: red;
  font-size: 0.9rem;
  margin-top: 5px;
  display: none;
}

input:user-invalid + .error-message {
  display: block;
}

input:invalid:not(:placeholder-shown) + .format-error-message {
  display: block;
}

/* ===============================================
# お問い合わせ確認
=============================================== */
.contact-form--confirm {
  margin-top: 2rem;
  padding-bottom: 3.8125rem;
}
@media screen and (min-width: 768px) {
  .contact-form--confirm {
    margin-top: 2.0625rem;
    padding-bottom: 2.4375rem;
  }
}

.contact-form--confirm .contact-form__bg {
  top: -10vw;
}
@media screen and (min-width: 768px) {
  .contact-form--confirm .contact-form__bg {
    top: -6.8vw;
  }
}

.contact-form__list--confirm {
  display: flex;
  flex-direction: column;
  gap: 2.25rem;
  margin-top: 3.9375rem;
}
@media screen and (min-width: 768px) {
  .contact-form__list--confirm {
    margin-top: 2.75rem;
    gap: 3.3125rem;
  }
}
.contact-form__list--confirm .contact-form__label {
  pointer-events: none;
}
@media (max-width: 767px) {
  .contact-form__list--confirm .contact-form__item {
    gap: 1.25rem;
  }
}
@media screen and (min-width: 768px) {
  .contact-form__list--confirm .contact-form__item {
    grid-template-columns: 15.625rem auto;
  }
}

@media (max-width: 767px) {
  .contact-form__confirm-text {
    margin-left: 1.0625rem;
  }
}

@media screen and (min-width: 768px) {
  .contact-form--confirm .contact-form__item--textarea dt {
    margin-top: 0.0625rem;
  }
}

.line-break {
  display: block;
}

.contact-form__confirm-submit {
  margin-top: 5.3125rem;
  align-items: center;
  display: grid;
  grid-template-columns: 1fr;
}
@media (max-width: 767px) {
  .contact-form__confirm-submit {
    gap: 2.3125rem;
  }
}
@media screen and (min-width: 768px) {
  .contact-form__confirm-submit {
    margin-top: 8.125rem;
    grid-template-columns: max-content auto;
    margin-left: 0;
    gap: 3.0625rem;
    position: relative;
    left: 1rem;
  }
}

@media (max-width: 767px) {
  .contact-form__confirm-submit-back-btn,
  .contact-form__confirm-submit-btn {
    text-align: center;
  }
}

.contact-form__confirm-submit-back {
  color: var(--main-yellow);
  font-size: 0.9375rem;
  font-weight: var(--font-weight-normal);
  line-height: 1.6;
  text-indent: 2em;
  letter-spacing: 2em;
  border-radius: calc(infinity * 1px);
  background-color: var(--white);
  border: 2px solid var(--main-yellow);
  padding: 1.0625rem 0.9375rem;
  min-width: 11rem;
  text-align: center;
  position: relative;
  width: fit-content;
  transition: background-color var(--duration-base) ease-in-out, color var(--duration-base) ease-in-out;
}
@media (any-hover: hover) {
  .contact-form__confirm-submit-back:hover {
    background-color: var(--main-yellow);
    color: var(--white);
  }
}
@media (any-hover: hover) {
  .contact-form__confirm-submit-back:hover::after {
    background-color: var(--white);
    right: 1.1875rem;
  }
}
.contact-form__confirm-submit-back::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 1.5625rem;
  transform: translateY(-50%);
  width: 0.5375rem;
  background-color: var(--main-yellow);
  border-radius: calc(infinity * 1px);
  aspect-ratio: 1/1;
  transition: background-color var(--duration-base) ease-in-out, right var(--duration-base) ease-in-out;
  will-change: right, background-color;
}

@media screen and (min-width: 768px) {
  .contact-form--confirm .contact-form__item.contact-form__item--textarea {
    align-items: flex-start;
  }
}

.contact-form--confirm .contact-form__item.contact-form__item--detail-none {
  display: none;
}

.contact-confirm__link-wrap {
  margin-top: 3.8125rem;
}
@media screen and (min-width: 768px) {
