.gyoza__bg {
  position: absolute;
  top: -8vw;
  left: 0;
  width: 100%;
  height: fit-content;
  object-fit: cover;
  aspect-ratio: 1399/321;
  z-index: -1;
}
@media screen and (min-width: 768px) {
