  grid-template-columns: 1fr;
  grid-template-areas: "img" "content";
}
@media screen and (min-width: 768px) {
  .gyoza__item {
    grid-template-columns: 27.25rem 1fr;
    gap: 4.9375rem;
    align-items: center;
    margin-right: calc(50% - 50cqw);
    grid-template-areas: "content img";
  }
}

